@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  font-family: "Raleway", sans-serif;
}
.style_container__RxNnR {
  position: absolute;
  width: 1024px;
  height: 800px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin: 0 auto;
  text-align:center;
}
.style_task__VxqvW{
    position: relative;
    background-color: #b31984;
    color:whitesmoke;
    padding:10px 20px;
    width:400px;
    height:50px;
    border-radius: 10px;
    margin:20px auto;
    transition: 0.3s ease-in-out;
}
.style_task__VxqvW input{
    display:inline;
    background-color: transparent;
    border:0;
    outline:none;
    float: left;
    font-size: 1em;
    padding-top: 15px;
}
.style_task__VxqvW img{
    float:right;
    cursor:pointer;
    padding-top: 15px;
}
.style_addField__2iZjT{
  position: relative;
  background-color: #445bbb;
  color:#d1cdca;
  padding: 20px;
  font-size: 1.4em;
  border-radius: 10px;
}
.style_addBtn__3hFgZ{
    position: relative;
    background-color: #c1cc0e;
    color:black;
    padding: 10px 20px;
    font-size: 1.4em;
    border-radius: 10px;
    cursor:pointer;
}
.style_title__3Pt_p {
  position: relative;
  background-color: purple;
  color: rgb(255, 255, 255);
  padding: 20px;
  font-size: 1.8em;
  border-radius: 5px;
  text-align: center;
}
@media (max-width:600px) {
  .style_container__RxNnR{
    width: 500px;
  }
  .style_addField__2iZjT{
    width:400px;
  }
  .style_task__VxqvW {
    width:250px;
  }
}
@media (max-width:300px) {
  .style_container__RxNnR{
    width: 250px;
  }
  
}

