@import url("https://fonts.googleapis.com/css?family=Raleway");

* {
  font-family: "Raleway", sans-serif;
}
.container {
  position: absolute;
  width: 1024px;
  height: 800px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin: 0 auto;
  text-align:center;
}
.task{
    position: relative;
    background-color: #b31984;
    color:whitesmoke;
    padding:10px 20px;
    width:400px;
    height:50px;
    border-radius: 10px;
    margin:20px auto;
    transition: 0.3s ease-in-out;
}
.task input{
    display:inline;
    background-color: transparent;
    border:0;
    outline:none;
    float: left;
    font-size: 1em;
    padding-top: 15px;
}
.task img{
    float:right;
    cursor:pointer;
    padding-top: 15px;
}
.addField{
  position: relative;
  background-color: #445bbb;
  color:#d1cdca;
  padding: 20px;
  font-size: 1.4em;
  border-radius: 10px;
}
.addBtn{
    position: relative;
    background-color: #c1cc0e;
    color:black;
    padding: 10px 20px;
    font-size: 1.4em;
    border-radius: 10px;
    cursor:pointer;
}
.title {
  position: relative;
  background-color: purple;
  color: rgb(255, 255, 255);
  padding: 20px;
  font-size: 1.8em;
  border-radius: 5px;
  text-align: center;
}
@media (max-width:600px) {
  .container{
    width: 500px;
  }
  .addField{
    width:400px;
  }
  .task {
    width:250px;
  }
}
@media (max-width:300px) {
  .container{
    width: 250px;
  }
  
}
